import { Table, TableProps, Tag } from '@/components'
import { Space } from 'antd'

const columns = [
  { title: '名称', dataIndex: 'name' },
  { title: '描述', dataIndex: 'description' },
  {
    title: '类型',
    dataIndex: 'type',
    render: tags => {
      if (tags instanceof Array) {
        return (
          <Space>
            {tags.map(tag => (
              <Tag>{tag}</Tag>
            ))}
          </Space>
        )
      } else {
        return <Tag>{tags}</Tag>
      }
    }
  },
  { title: '默认值', dataIndex: 'defaultValue' },
  { title: '是否必填', dataIndex: 'required' }
]

export const ApiTable = (props: TableProps) => {
  return <Table size="small" columns={columns} pagination={false} {...props} />
}
